.calculator_container {
  display: flex;
  flex-direction: column;
  background-color: $White;
  max-width: 70vh;
  font-family: $font;
  padding: 2.5rem;
  border-radius: 20px 20px 180px 20px;
  margin: 10px;

  @media screen and (max-width: 700px) {
    padding: 2.5rem, 0.5rem;
  }

  .age_form {
    form {
      div {
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        @media screen and (max-width: 700px) {
          grid-template-columns: repeat(3, 1fr);
        }
        span {
          display: flex;
          flex-direction: column;

          label {
            font-size: 14px;
            letter-spacing: 2px;
            color: $Smokeygrey;
          }

          .error-label {
            color: $lightred;
          }

          input {
            width: 80%;
            height: 30px;
            border: 1px solid $Lightgrey;
            font-size: 20px;
            font-weight: 600;
            border-radius: 5px;
            padding: 6px;
            cursor: pointer;
            color: $Offblack;

            &:hover {
              border: 1px solid $purple;
            }

            &:focus {
              border: 1px solid $purple;
              outline: none;
              background-color: $White;
            }
          }

          .error-input {
            border: 1px solid $lightred;
          }

          .box-error {
            width: 100%;
            display: flex;
            height: 10px;
            text-align: center;

            .error {
              color: $lightred;
              font-size: 12px;

              @media screen and (max-width: 700px) {
                font-size: 10px;
              }
            }
          }
        }
      }
    }

    .seperate-btn-box {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      box-sizing: border-box;
      margin-top: 20px;
      align-items: center;
      justify-content: center;

      @media screen and (max-width: 700px) {
        display: flex;
        width: 100%;
        position: relative;
        margin-top: 60px;
      }

      .hr-box {
        width: 100%;
        grid-column: 1/5;

        hr {
          border-top: 1px solid $Lightgrey;
          width: 100%;
        }
      }

      .btn-box {
        grid-column: 5/6;
        display: flex;

        @media screen and (max-width: 700px) {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          grid-column: none;
        }

        button {
          border-radius: 50%;
          width: 80px;
          height: 80px;
          border: none;
          outline: none;
          background-color: $purple;
          cursor: pointer;

          &:hover {
            background-color: $Offblack;
          }

          img {
            width: 50%;
          }

          @media screen and (max-width: 700px) {
            width: 60px;
            height: 60px;
          }
        }
      }
    }
  }

  .result_container {
    display: flex;
    width: 100%;
    flex-direction: column;
    font-family: $fontItalic;
    margin: 6px;

    @media screen and (max-width: 700px) {
      margin-top: 40px;
    }
    span {
      display: flex;
      margin: 0;
      .result {
        font-size: 4em;
        margin: 0 6px 0 0;
        color: $purple;

        @media screen and (max-width: 700px) {
          font-size: 2.5em;
        }
      }

      .unit {
        font-size: 4em;
        margin: 0;

        @media screen and (max-width: 700px) {
          font-size: 2.5em;
        }
      }
    }
  }
}
